import React, { Fragment } from "react";
import { connect } from "react-redux";
import PropTypes from 'prop-types';

function UsersListPage({usersList}) {
  return (
    <Fragment>
      <ul>
        {
          usersList.map(user => {
            return `<li>${user.name}: ${user.status}</li>`
          })
        }
      </ul>
    </Fragment>
  );
}

UsersListPage.propTypes = {
  usersList: PropTypes.arrayOf(PropTypes.object)
}

const mapStateToProps = (state) => {
  return {
    usersList: state.userReducer.usersList
  }
}

const mapStateToDispatch = (dispatch) => {
  return {
    getUser: (id) => {
      getUser(id).then(data => dispatch(actions.setUser(data)));
    }
  }
}

export default connect(mapStateToProps, mapStateToDispatch)(UsersListPage);